<template>
  <div style="overflow: hidden">
    <div v-if="!isaffrim">
      <div class="mhead" style="height: 8%;padding-top: 2%;">
        <ul>
          <li>
            <text style="position: absolute;right: 0px">查询编号</text>
           </li>
        </ul>
      </div>
      <div class="searchview auto">
        <div class="form-group row auto" style="margin-top:10px;">
          <div class="col-xs-9">
            <label  class="col-xs-4 control-label" style="font-size:18px" ><nobr>用户姓名</nobr></label>
            <div class="col-xs-7">
              <input type="text" class="form-control"  v-model="f_user_name"
                     condition="f_userid = {} " placeholder="请输入开户人姓名"/>
            </div>
          </div>
          <div class="col-xs-3" >
            <button  type="button" class="btn btn-sm btn-primary"  @click="search()"> 查询 </button>
          </div>
        </div>
        <div style="height: 5%;">
          <table class="table table-bordered self-table">
            <thead style="background-color: #499edf">
              <tr class="table-tr-1">
                <th style="text-align:center;"><nobr>用户名称</nobr></th>
                <th style="text-align:center;"><nobr>用户编号</nobr></th>
              </tr>
            </thead>
            <tbody v-for = "row in model.rows">
              <tr>
                <td style="text-align:center;"><p class="name">{{row.f_user_name}}</p></td>
                <td style="text-align:center;"><p class="address">{{row.f_userinfo_code}}</p></td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="foot" style="margin-top: 124%;" @click="back('tomain')">
          <text>返回</text>
        </div>
      </div>
    </div>
    <user-info v-if="isaffrim" :userinfo="userInfo" :cardinfo="cardInfo"></user-info>
  </div>
</template>
<script>
  import Vue from "vue";
  let getuser = function* (self) {
    let request = {
      condition : '1=1 and  f_user_name like \'%'+self.f_user_name+'%\' and f_card_id =\''+self.cardData.CardID+'\' order by f_user_id desc'
    }
    let result = yield self.$resetpost(Vue.ProxyUrl + '/rs/sql/'+Vue.PosUtil.posParam.revenue+'getUserInfo', {data: request}, {resolveMsg: null, rejectMsg: null})
    if (result){
      if (result.data.length === 0){
        self.$showMessage("您输入的姓名和卡信息不匹配！")
      }
      self.model.rows = result.data
    }
  }
  import co from 'co'
  export default {
    title: '编号查询',
    data () {
      return {
        isaffrim: false,
        userInfo: {},
        cardInfo: {},
        f_user_name:'',
        model: {rows: []},
      }
    },
    props: ['sellType','cardData'],
    methods: {
        search(){
           if(this.f_user_name === ''){
               this.$showAlert('请先输入用户姓名', 'warning', 1500)
               return
           }
          this.model.rows = []
          co(getuser(this))
        },
      back (param) {
        this.$back(param)
      },
      callBack(param){
          if(param === "toSelect"){
            return
          }
          this.$back(param)
      },
    },
    events: {

    },
    ready () {

    }
  }
</script>



